.notes__main-content {
  display: flex;
  flex-direction: column;
  height: 100vh;
}

.notes__appbar {
  align-items: center;
  background-color: $primary;
  color: $white;
  display: flex;
  justify-content: space-between;
  padding: 10px 15px;
}

.notes__content {
  display: flex;
  flex-direction: column;
  height: 100%;
  padding: 20px;
}

.notes__title-input,
.notes__textarea {
  border: none;
  color: $dark-grey;
  &:focus {
    outline: none;
  }
}

.notes__title-input {
  font-size: 25px;
  font-weight: bold;
  margin-bottom: 10px;
}

.notes__textarea {
  font-size: 20px;
  flex: 1 1 auto;
  resize: none;
}

.notes__image {
  img {
    box-shadow: 5px 5px $dark-grey;
    height: 150px;
    max-width: 150px;
  }
}
